.body {
  background-color: white;
  width: 70%;
  height: 100%;
  padding: 32px 0;
  margin: 16px auto;
  text-align: center; }

.kitty {
  -webkit-animation: logo-spin infinite 60s linear;
          animation: logo-spin infinite 60s linear;
  height: 80px; }

@-webkit-keyframes logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }



